import { Logger, getWindow } from '@sitecore-search/common';
import { ContextEventStorage, getUserId, setup as setupDataLayer } from '@sitecore-search/data';
import Actions from './actions.js';
import PageController from './controller/page.js';
import WidgetsConfigDataProviderProcessor from './dataProviderProcessor.js';
import WidgetsConfig from './widgetsConfig.js';
export const setCredentials = (dataOptions) => {
    setupDataLayer(dataOptions, { processor: new WidgetsConfigDataProviderProcessor() });
};
let initialized = false;
export const init = () => {
    if (initialized) {
        return;
    }
    PageController.getContext().setUserUuid(getUserId().uuid);
    ContextEventStorage.storage.browser.set('user_agent', getWindow({ navigator: {} }).navigator.userAgent);
    initialized = true;
    PageController.getDispatcher().dispatch({
        type: Actions.INITIALIZE,
        payload: {
            options: {},
        },
    });
};
export const setWidgetType = (type, mapping) => {
    Logger.debug('setWidgetType', `Type: ${type}`, `Mapping: ${JSON.stringify(mapping)}`);
    WidgetsConfig.setWidgetType(type, mapping);
};
export function setWidget(rfkId, entityOrConfig, config) {
    if (typeof entityOrConfig === 'string') {
        Logger.info('setWidget', rfkId, entityOrConfig, JSON.stringify(config));
        WidgetsConfig.update(rfkId, entityOrConfig, config);
    }
    else {
        Logger.info('setWidget', rfkId, JSON.stringify(config));
        WidgetsConfig.update(rfkId, config);
    }
}
