export const CSS_VARIABLES_PREFIX = 'sdc';
// converts the nested theme object into a flat object with `--path-to-value` keys
export const jsonToCSSVars = (obj, prefix = CSS_VARIABLES_PREFIX) => {
    const vars = {};
    for (const [key, value] of Object.entries(obj)) {
        if (typeof value === 'object') {
            const nestedVars = jsonToCSSVars(value, `${prefix}-${key}`);
            for (const [nestedKey, nestedValue] of Object.entries(nestedVars)) {
                vars[nestedKey] = nestedValue;
            }
        }
        else {
            vars[`--${prefix ? `${prefix}-` : ''}${key}`] = value;
        }
    }
    return vars;
};
// converts the nested theme object with theme values into one with
// the theme variables as the value
export const jsonToCSSVarNames = (obj, prefix = CSS_VARIABLES_PREFIX) => {
    const vars = {};
    for (const [key, value] of Object.entries(obj)) {
        if (typeof value === 'object') {
            vars[key] = jsonToCSSVarNames(value, `${prefix}-${key}`);
        }
        else {
            vars[key] = `var(--${prefix ? `${prefix}-` : ''}${key})`;
        }
    }
    return vars;
};
