/**
 * @desc
 * This event must be sent when a user logs in to the system.
 * This event is used in linking real user id to a Reflektion cookie so it would help
 * in more advanced user personalization use cases like cross device personalization
 * @param userModel id & email
 * @returns {Promise<*>}
 */
export const trackUserLoginEvent = async (userModel, actionSubtype) => {
    if (!userModel || emptyObject(userModel)) {
        Logger.warn('trackEvent :::', 'User information is empty or not complete');
        return;
    }
    const contextValue = mergeDeep(ContextEventStorage.toJson(), {
        user: {
            ...(userModel.id ? { cuid: userModel.id } : {}),
            ...(userModel.email ? { email: userModel.email } : {}),
        },
    });
    const params = {
        action: 'identify',
        name: 'login',
        action_sub_type: actionSubtype,
        value: {
            context: contextValue,
        },
    };
    return track(params);
};
